.navbar {
    background-color: #111 !important;

    .nav-item {
        .active:hover {
            color: orange !important;
        }

        .icon {
            height: 1.1rem;
            width: auto;
        }
    }

}

$iconHeight: 1.5rem;
$marginX: 1rem;

.footer {
    background-color: rgb(13, 17, 23);

    .wolf-hand {
        position: absolute;
        height: 5rem;
        width: auto;
        filter: drop-shadow(5px 5px 5px gray);
    }

    .footer-img {
        height: $iconHeight;
        width: auto;
        margin-left: $marginX;
        margin-right: $marginX;
    }
}

.login-page {
    height: 100vw !important;
    width: 100vw !important;
    position: fixed;
    z-index: 999999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #444;
  }